.burbuja{
  position: absolute;
  width: 30vh;
  height: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  left: 5vw;
  animation: bubblesss 15s cubic-bezier(0.32, 0, 0.67, 0) infinite;
  -webkit-animation: bubblesss 15s cubic-bezier(0.32, 0, 0.67, 0) infinite;
}

.burbuja img{
  border-radius: 50%;
}

.yo{
  position: absolute;
  width: 30vh;
  animation: yo 15s cubic-bezier(0.16, 1, 0.3, 1) 1s infinite normal none;
  -webkit-animation: yo 15s cubic-bezier(0.16, 1, 0.3, 1) 1s infinite normal none;
}

@keyframes yo {
	0% {
		opacity: 0;
		transform: translateX(250px);
		-webkit-transform: translateX(250px);
		-moz-transform: translateX(250px);
		-ms-transform: translateX(250px);
		-o-transform: translateX(250px);
}

	25% {
		opacity: 1;
		transform: translateX(0);
		-webkit-transform: translateX(0);
		-moz-transform: translateX(0);
		-ms-transform: translateX(0);
		-o-transform: translateX(0);
}

  50%{
    transform: translateX(-250px);
    opacity: 0;
    -webkit-transform: translateX(-250px);
    -moz-transform: translateX(-250px);
    -ms-transform: translateX(-250px);
    -o-transform: translateX(-250px);
  }

  100%{
    opacity: 0;
  }
}


@keyframes bubblesss {
	0% {
		transform: scale(0.2);
		opacity: 0;
		-webkit-transform: scale(0.2);
		-moz-transform: scale(0.2);
		-ms-transform: scale(0.2);
		-o-transform: scale(0.2);
}
	40% {
    opacity: 1;
	}
  55%{
    transform: scale(1);
    opacity: 0;
  }
  100%{
    opacity: 0;
  }
}

.burbuja img{
  position: absolute;
  z-index: 1;
  width: 100%;
}

.burbuja video{
  border-radius: 50%;
  height: 30vh;
