}

.overlay{
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(0,0,0, 0.6);
}

  .Titulo {
    z-index: 2;
    position: absolute;
    animation: myAnim 1s ease 0s 1 normal forwards;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: blanchedalmond;
    font-weight: 10;
    font-size: 10vh;
}

  .SubTitulo {
    z-index: 2;
    position: absolute;
    animation: myAnim 1s ease 0s 1 normal forwards;
    width: 100%;
    height: 100%;
    top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: blanchedalmond;
    font-weight: 10;
    font-size: 5vh;
}

  @keyframes entradas {
	0% {
		opacity: 0;
		transform: translateX(250px);
	}

	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes myAnim {
	0% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}

}

.container video{
  object-fit: cover;
}

